import React from 'react';
import './styles.css'

function Entry(props){
    return(
        <div className="term">
            <div className="emoji" role="img" aria-label="Tense Biceps">
                {props.emoji}
            </div>
            <div className="name">
                <h2>{props.name}</h2>
            </div>
            <div className="description">
                <p>{props.description}</p>
            </div>  
        </div>
    )
}

export default Entry;