import React from 'react';
import Entry from './Entry';
import emoji from '../emoji';
import './styles.css'

function createEntry(emojiTerm){
    return(
    <Entry
        key = {emojiTerm.id}
        emoji ={emojiTerm.emoji}
        name ={emojiTerm.name}
        description ={emojiTerm.description}
    />
    ) 
}

function App(){
    return(
        <div>
            <h1>
                <div>Emojipedia</div>
            </h1>
            <dl className="dictionary">
                {emoji.map(createEntry)}
            </dl>
        </div>
    )
}

export default App;